import { useAuth0 } from "@auth0/auth0-react";
import { useContext } from "react";
import styled from "styled-components";
import useMedia from "use-media";
import SystemUtil from "../../utils/systemUtil";
import AccountManage from "../account/accountManage";
import Regulation from "../define/defineManage";
import { GlobalContext } from "../entry/entry";
import { MediaQueryContext } from "../entry/manageFrame";
import SearchContents from "../seach/searchContents";
import MenuCard from "./menuCard";

const Entrance = () => {
    const { store, setStore } = useContext(GlobalContext);
    // const mediaQuery = useContext(MediaQueryContext);

    const isLogin = store.user != null;
    return (
        <_Wrap>
            <MenuCard
                title={''}
                isEnable={isLogin}
                detail={<><br /></>}
                callback={() => {
                    // store.mode = 'regulation';
                    store.transition.setNextFrame(<Regulation masterConteSeq={-1} />);
                    setStore({ ...store });
                }}
            />
            <MenuCard
                title={''}
                isEnable={isLogin}
                detail={<></>}
                callback={() => {
                    // store.mode = 'search';
                    store.transition.setNextFrame(<SearchContents />);
                    setStore({ ...store });
                }}
            />
            <MenuCard
                title={''}
                isEnable={isLogin}
                detail={<>{[
                    '',
                    '',
                    'URL'
                ].map((str, i) => <span key={i}>{str}<br /></span>)}</>}
                callback={() => {
                    // store.mode = 'regulation';
                    setStore({ ...store });
                }}
            />
            <MenuCard
                title={''}
                isEnable={isLogin}
                detail={<>{[
                    'ID',
                ].map((str, i) => <span key={i}>{str}<br /></span>)}</>}
                callback={() => {
                    // store.mode = 'account';
                    store.transition.setNextFrame(<AccountManage />);
                    setStore({ ...store });
                }}
            />
        </_Wrap>
    );
}

export default Entrance;

const _Wrap = styled.div`
    display: inline-block;
    width: 100%;
    max-width: 960px;
    height: 100%;
    background-color: #f3f3f328;
    text-align: center;
`;